@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@700&display=swap);
:root {
/* ### Theme 1 */

/* #### Backgrounds */

--desaturated-blue: hsl(222, 26%, 31%); /*(main background)*/
--dark-desaturated-blue: hsl(223, 31%, 20%); /*(toggle background, keypad background)*/
--very-dark-desaturated-blue: hsl(224, 36%, 15%); /*(screen background)*/

/* #### Keys */

--desaturated-dark-blue: hsl(225, 21%, 49%);  /*(key background)*/
--desaturated-dark-blue: hsl(224, 28%, 35%);  /*(key shadow)*/
--red: hsl(6, 63%, 50%);  /*(key background, toggle)*/
--dark-red: hsl(6, 70%, 34%);  /*(key shadow)*/

--light-grayish-orange: hsl(30, 25%, 89%);  /*(key background)*/
--grayish-orange: hsl(28, 16%, 65%);  /*(key shadow)*/

/* #### Text */

--very-dark-grayish-blue: hsl(221, 14%, 31%);
--white:hsl(0, 0, 100%);

/* ### Theme 2 */

/* #### Backgrounds */
--light-gray: hsl(0, 0%, 90%); /*(main background)*/
--grayish-red: hsl(0, 5%, 81%); /*(toggle background, keypad background)*/
--very-light-gray: hsl(0, 0%, 93%); /*(screen background)*/

/* #### Keys */

--dark-moderate-cyan: hsl(185, 42%, 37%);
--very-dark-cyan: hsl(185, 58%, 25%);

--Orange: hsl(25, 98%, 40%);
--dark-orange: hsl(25, 99%, 27%);

--light-grayish-yellow: hsl(45, 7%, 89%);
--dark-grayish-orange: hsl(35, 11%, 61%);

/* #### Text */

--very-dark-grayish-yellow: hsl(60, 10%, 19%);
--white: hsl(0, 0, 100%);

/* ### Theme 3 */

/* #### Backgrounds */
--very-dark-violet: hsl(268, 75%, 9%);
--very-dark-violet: hsl(268, 71%, 12%);  /*(toggle background, keypad background, screen background)*/

/* #### Keys */

--dark-violet: hsl(281, 89%, 26%);
--vivid-magenta: hsl(285, 91%, 52%);

--Pure-cyan: hsl(176, 100%, 44%);
--Soft-cyan: hsl(177, 92%, 70%);

--very-dark-violet: hsl(268, 47%, 21%);
--dark-magenta: hsl(290, 70%, 36%);

/* #### Text */

--light-yellow: hsl(52, 100%, 62%);
--very-dark-blue: hsl(198, 20%, 13%);
--white: hsl(0, 0, 100%);
}

* {
    margin: 0;
    padding: 0;
    border: 0;
    box-sizing: border-box;
}

html, body {
    font-family: 'Spartan', sans-serif;
    overflow: hidden;
    background-color: hsl(222, 26%, 31%);
    background-color: var(--desaturated-blue);
    color: white;
    width: 100vw;
    height: 100vh;
    display: grid;
    justify-content: center;
    align-items: center;
}

